import "./styles.css";
import ImageGallery from "react-image-gallery";
import "react-image-gallery/styles/css/image-gallery.css";
import { useRef } from "react";
import rosie1 from "./Images/rosie1.JPG";
import rosie2 from "./Images/rosie2.JPG";
import rosie3 from "./Images/rosie3.JPG";

export default function App() {
  const images = [
    {
      original: rosie1,
      thumbnail: rosie1
    },
    {
      original: rosie2,
      thumbnail: rosie2
    },
    {
      original: rosie3,
      thumbnail: rosie3
    }
  ];

  const play = useRef();
  function playSlides() {
    play.current.play();
  }

  return (
    <div className="App">
      <h1>rosie the cockatiel</h1>
      <ImageGallery
        ref={play}
        items={images}
        showPlayButton={true}
        showFullscreenButton={true}
        slideInterval={2000}
        slideOnThumbnailOver={true}
        showIndex={true}
        onPlay={() => {
          alert("slideshow playing! :)");
        }}
      />

      <button onClick={playSlides}>play slideshow!</button>
    </div>
  );
}
